.line-through{text-decoration:line-through;user-select:none}.contactPage{text-align:center;margin:50px 0 75px 0}.contactPage h2{font-size:4.5em;font-family:Roboto Slab,serif;margin-bottom:.27778em;color:red;line-height:1.2em}.contactPage p{font-size:1.6em;font-family:Roboto Mono,monospace;margin-bottom:1.5625em;color:#6b6a6a;line-height:1.3em}.contactPage form{max-width:480px;margin:0 auto}.contactPage label{display:inline-block;font-size:1.8em;font-family:Roboto Mono,monospace;margin-bottom:.34722em;font-weight:300}.contactPage input{display:inline-block;width:100%;border:1px solid #979797;min-height:50px;padding:10px}.contactPage textarea{display:inline-block;width:100%;border:1px solid #979797;min-height:200px;padding:15px}.contactPage input,.contactPage textarea{font-size:1.6em;font-family:Roboto Mono,monospace;margin-bottom:0}.contactPage input:not([type='submit']),.contactPage textarea{margin-bottom:30px}.contactPage input[type='submit']{font-size:1.8em;font-family:Roboto Mono,monospace;margin-bottom:0;color:white;background:#da542f}
